@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/unbuntu-regular.woff2") format("woff2"), url("../fonts/unbuntu-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ubuntu-bold.woff2") format("woff2"), url("../fonts/ubuntu-bold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  margin: 0 auto;
}
@media screen and (min-width: 1200px) {
  .page__body {
    background-color: #f2f2f2;
  }
}

.page__container {
  min-width: 320px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: #FFFFFF;
  color: #000000;
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .page__container {
    width: 768px;
  }
}
@media screen and (min-width: 1200px) {
  .page__container {
    width: 1200px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  }
}

@media screen and (min-width: 768px) {
  .page__wrapper {
    padding: 0 60px;
  }
}

.button {
  font-family: "Ubuntu", "Arial", sans-serif;
  background-color: #FFFFFF;
  color: #000000;
  line-height: 22px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
  border-radius: 100px;
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
  cursor: pointer;
}
.button--secondary {
  background: linear-gradient(180deg, #8EE902 0%, #4F9C2C 100%);
  padding-left: 30px;
  padding-right: 30px;
  text-decoration: none;
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 14px;
  display: inline-block;
  width: 120px;
}
.button:hover {
  color: #60B021;
}
.button:active {
  background-color: #5496bd;
}
.button--search {
  width: 284px;
  padding-top: 16px;
  padding-bottom: 16px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .button--search {
    width: 458px;
    padding-top: 14px;
    padding-bottom: 17px;
  }
}

.main-nav {
  position: relative;
  z-index: 2;
}
@media screen and (max-width: 767px) {
  .main-nav {
    min-height: 79px;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 25px;
  right: 0;
  z-index: 2;
  display: block;
  width: 57px;
  height: 55px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 23px;
  height: 4px;
  background-color: #60B021;
  box-shadow: 0 9px 0 0 #60B021, 0 18px 0 0 #60B021;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 0;
  z-index: 11;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 285px;
  height: 3px;
  width: 23px;
  background-color: #60B021;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::after, .main-nav--opened .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::after, .main-nav--opened .main-nav__toggle:active::before {
  opacity: 0.3;
}

@media screen and (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    width: 320px;
    z-index: 10;
    background-color: #F5F5F5;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.site-list {
  list-style-type: none;
  background-color: #F5F5F5;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  margin: 0;
  position: relative;
}
@media screen and (min-width: 768px) {
  .site-list {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    background-color: #F5F5F5;
    color: #000000;
    margin: 0;
  }
}
.site-list::after {
  content: "";
  position: absolute;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  background-repeat: no-repeat;
  height: 7px;
  bottom: 0;
}
@media screen and (min-width: 768px) {
  .site-list::after {
    display: none;
  }
}

.site-list__item {
  text-transform: uppercase;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.site-list__item:last-child {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media screen and (max-width: 767px) {
  .site-list__item:hover {
    background-color: #669ec0;
  }
  .site-list__item:active {
    background-color: #5496bd;
