    box-shadow: none;
  }
}

.site-list__link {
  display: block;
  color: #000000;
  text-decoration: none;
  font-size: 18px;
  line-height: 26px;
  padding: 17px 83px 12px 81px;
}
@media screen and (min-width: 768px) {
  .site-list__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding: 18px 0 17px;
    text-align: center;
  }
}
@media screen and (min-width: 768px) {
