.fialka__title-link {
  color: $basic-text;
  text-decoration: none;
}

.fialka {
    width: 300px;
    border: 1px solid #E3E3E3;
  @include tablet {
    width: 320px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
  }
  @include desktop {
    width: 350px;
  }
}
.fialka__title {
    margin: 0;
    padding-bottom: 10px;
}
.fialka__conatiner {
    display: flex;
    margin-bottom: 10px;
}

.fialka__wrap-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 154px;
  padding: 9px;
  border: 1px solid #EFEFEF;
}

.fialka__button {
  width: 150px;
}

.fialka__price {
  font-weight: 700;
}

